import React from 'react'
import {Link} from 'react-router-dom'

import { Col, Container, Row } from '../Grid'
import './Footer.css'
// Depending on the current path, this component sets the "active" class on the appropriate navigation link item
const Footer = props =>{
	return (<Row>
		<div className="footer">
			<div className="footerTop">
			<Row>
			<Col size="sm-12">
				<Row>
					<div className="col-sm-12 mx-auto text-center footer-nav">
						<span className={window.location.pathname === '/Profile' ? 'active footer-item' : 'footer-item'}>
							<Link className='footer-link' to='/Profile'>Profile</Link>{` | `}
						</span>
						<span className={window.location.pathname === '/Link2' ? 'active footer-item' : 'footer-item'}>
							<Link className='footer-link' to='/Link2'>Link2</Link>{` | `}
						</span>
						<span className={window.location.pathname === '/Link3' ? 'active footer-item' : 'footer-item'}>
							<Link className='footer-link' to='/Link3'>Link3</Link>{` | `}
						</span>
						<span className={window.location.pathname === '/Link4' ? 'active footer-item' : 'footer-item'}>
							<Link className='footer-link' to='/Link4'>Link4</Link>
						</span>
					</div>

				</Row>
			</Col>
		</Row>
			</div>
			<div className="footerBot">
				<Row>
					<Col size="sm-12">
						<Row>
							<Col size="sm-2"></Col>
							<Col size="sm-2">
							
							
							</Col>
							<Col size="sm-2">
							
							
							</Col>
							<Col size="sm-2">
							
							
							</Col>
							<Col size="sm-2">
								
								<Row>
									<span className="footerLabel">Green Team</span>
								</Row>
								<Row>
								<a href="https://github.com/JakeGreer"  target="_blank"><span className="teamMember">Jake Greer</span></a>
								</Row>
								<Row>
								<a href="https://github.com/e-hermoso"  target="_blank"><span className="teamMember">Eric Hermoso</span></a>
								</Row>
								<Row>
								<a href="https://github.com/CoreyRo"  target="_blank"><span className="teamMember">Corey Ro</span></a>
								</Row>
								<Row>
								<a href="https://github.com/theRealScoobaSteve"  target="_blank"><span className="teamMember">Stephen Simone</span></a>
								</Row>
								
							
							</Col>
							<Col size="sm-2"></Col>
						</Row>
					</Col>
				</Row>
			</div>
		</div>
	</Row>)
}

export default Footer



